<template>
	<view class="content">


		<u-navbar back-icon-color="#5098FF" title="所有圈子"></u-navbar>

		<view class="slot-wrap">
			<u-search class="index_search" @clear="showcreateCircle" @blur="showcreateCircle"
				@focus="hiddlecreateCircle" @search="startSearch" @custom="creatCircle" :clearabled="true"
				v-model="search" placeholder="搜索圈子" :show-action="action" action-text="建圈"></u-search>
		</view>


		<view class="u-menu-wrap">
			<scroll-view scroll-y scroll-with-animation class="u-tab-view menu-scroll-view" :scroll-top="scrollTop"
				:scroll-into-view="itemId">
				<view v-for="(item,index) in tabbar" :key="index" class="u-tab-item"
					:class="[current == index ? 'u-tab-item-active' : '']" @tap.stop="swichMenu(index)">
					<text class="u-line-1">{{item.name}}</text>
				</view>
			</scroll-view>
			<scroll-view :scroll-top="scrollRightTop" scroll-y scroll-with-animation class="right-box"
				@scroll="rightScroll">
				<view class="page-view">
					<view class="class-item" :id="'item' + index" v-for="(item , index) in tabbar" :key="index">
						<view class="item-title">
							<text>{{item.name}}</text>
						</view>
						<view class="item-container">
							<view class="thumb-box" v-for="(item1, index1) in item.foods" :key="index1"
								@click="tocircle(item1)">
								<view class="item-menu-image">
									<u-image :src="item1.icon" height="100%"></u-image>
								</view>
								<view class="right">
									<view class="name">
										<i class="iconfont icon-jinghao"></i>
										{{item1.name}}
									</view>
									<view class="info">86条动态 | 346位校友关注</view>
									<view class="desc">简介:{{item1.name}}</view>
									<view class="selectBtn" v-if="showselectBtn" @click.stop="chooseCircle(item1)">选择
									</view>
								</view>
								<!-- <view class="item-menu-name">{{item1.name}}</view> -->
							</view>
						</view>
					</view>
				</view>
			</scroll-view>
		</view>


	</view>
</template>

<script>
	import classifyData from '@/testData/classify.data.js';

	import {
		mapMutations
	} from "vuex"


	export default {
		data() {
			return {
				scrollTop: 0, //tab标题的滚动条位置
				oldScrollTop: 0,
				current: 0, // 预设当前项的值
				menuHeight: 0, // 左边菜单的高度
				menuItemHeight: 0, // 左边菜单item的高度
				itemId: '', // 栏目右边scroll-view用于滚动的id
				tabbar: classifyData,
				menuItemPos: [],
				arr: [],
				scrollRightTop: 0, // 右边栏目scroll-view的滚动条高度
				timer: null, // 定时器
				search: '',
				action: true,
				showselectBtn: false

			}
		},
		onLoad(option) {

			console.log(option.type)

			if (option.type) {
				this.showselectBtn = true
			}
		},
		onReady() {
			this.getMenuItemTop()
		},
		methods: {
			...mapMutations(['changeCurrentSelect']),
			chooseCircle(item) {
				
				this.changeCurrentSelect(item.name)
				
				uni.navigateBack({
					delta:1
				})

			},
			hiddlecreateCircle() {
				//隐藏建圈
				this.action = false
			},
			showcreateCircle() {
				//显示建圈
				this.action = true
			},
			startSearch() {
				//搜索
				console.log(this.search)
			},
			creatCircle() {
				console.log("建圈")
			},
			tocircle(item) {
				//点击圈子
				console.log(item)
				uni.navigateTo({
					url: `../circledetail/circledetail?id=${item.cat}&name=${item.name}`
				})
			},
			// 点击左边的栏目切换
			async swichMenu(index) {
				if (this.arr.length == 0) {
					await this.getMenuItemTop();
				}
				if (index == this.current) return;
				this.scrollRightTop = this.oldScrollTop;
				this.$nextTick(function() {
					this.scrollRightTop = this.arr[index];
					this.current = index;
					this.leftMenuStatus(index);
				})
			},
			// 获取一个目标元素的高度
			getElRect(elClass, dataVal) {
				new Promise((resolve, reject) => {
					const query = uni.createSelectorQuery().in(this);
					query.select('.' + elClass).fields({
						size: true
					}, res => {
						// 如果节点尚未生成，res值为null，循环调用执行
						if (!res) {
							setTimeout(() => {
								this.getElRect(elClass);
							}, 10);
							return;
						}
						this[dataVal] = res.height;
						resolve();
					}).exec();
				})
			},
			// 观测元素相交状态
			async observer() {
				this.tabbar.map((val, index) => {
					let observer = uni.createIntersectionObserver(this);
					// 检测右边scroll-view的id为itemxx的元素与right-box的相交状态
					// 如果跟.right-box底部相交，就动态设置左边栏目的活动状态
					observer.relativeTo('.right-box', {
						top: 0
					}).observe('#item' + index, res => {
						if (res.intersectionRatio > 0) {
							let id = res.id.substring(4);
							this.leftMenuStatus(id);
						}
					})
				})
			},
			// 设置左边菜单的滚动状态
			async leftMenuStatus(index) {
				this.current = index;
				// 如果为0，意味着尚未初始化
				if (this.menuHeight == 0 || this.menuItemHeight == 0) {
					await this.getElRect('menu-scroll-view', 'menuHeight');
					await this.getElRect('u-tab-item', 'menuItemHeight');
				}
				// 将菜单活动item垂直居中
				this.scrollTop = index * this.menuItemHeight + this.menuItemHeight / 2 - this.menuHeight / 2;
			},
			// 获取右边菜单每个item到顶部的距离
			getMenuItemTop() {
				new Promise(resolve => {
					let selectorQuery = uni.createSelectorQuery();
					selectorQuery.selectAll('.class-item').boundingClientRect((rects) => {
						// 如果节点尚未生成，rects值为[](因为用selectAll，所以返回的是数组)，循环调用执行
						if (!rects.length) {
							setTimeout(() => {
								this.getMenuItemTop();
							}, 10);
							return;
						}
						rects.forEach((rect) => {
							// 这里减去rects[0].top，是因为第一项顶部可能不是贴到导航栏(比如有个搜索框的情况)
							this.arr.push(rect.top - rects[0].top);
							resolve();
						})
					}).exec()
				})
			},
			// 右边菜单滚动
			async rightScroll(e) {
				this.oldScrollTop = e.detail.scrollTop;
				if (this.arr.length == 0) {
					await this.getMenuItemTop();
				}
				if (this.timer) return;
				if (!this.menuHeight) {
					await this.getElRect('menu-scroll-view', 'menuHeight');
				}
				setTimeout(() => { // 节流
					this.timer = null;
					// scrollHeight为右边菜单垂直中点位置
					let scrollHeight = e.detail.scrollTop + this.menuHeight / 2;
					for (let i = 0; i < this.arr.length; i++) {
						let height1 = this.arr[i];
						let height2 = this.arr[i + 1];
						// 如果不存在height2，意味着数据循环已经到了最后一个，设置左边菜单为最后一项即可
						if (!height2 || scrollHeight >= height1 && scrollHeight < height2) {
							this.leftMenuStatus(i);
							return;
						}
					}
				}, 10)
			}
		}
	}
</script>

<style lang="scss">
	page {
		width: 100%;
		height: 100%;

		.content {
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;

			.slot-wrap {
				display: flex;
				align-items: center;
				width: 100%;
				/* 如果您想让slot内容占满整个导航栏的宽度 */
				/* flex: 1; */
				/* 如果您想让slot内容与导航栏左右有空隙 */
				padding: 0 30rpx;

				.index_search {
					width: 100%;
				}
			}



			.u-menu-wrap {
				flex: 1;
				display: flex;
				overflow: hidden;
				// position: relative;
				// background-color: pink;




				.u-tab-view {
					width: 200rpx;
					height: 100%;
				}

				.u-tab-item {
					height: 110rpx;
					background: #f6f6f6;
					box-sizing: border-box;
					display: flex;
					align-items: center;
					justify-content: center;
					font-size: 26rpx;
					color: #444;
					font-weight: 400;
					line-height: 1;
				}

				.u-tab-item-active {
					position: relative;
					color: #000;
					font-size: 30rpx;
					font-weight: 600;
					background: #fff;
				}

				.u-tab-item-active::before {
					content: "";
					position: absolute;
					border-left: 4px solid $u-type-primary;
					height: 32rpx;
					left: 0;
					top: 39rpx;
				}

				.u-tab-view {
					height: 100%;
				}

				.right-box {
					background-color: rgb(250, 250, 250);
				}

				.page-view {
					padding: 16rpx;
				}

				.class-item {
					margin-bottom: 30rpx;
					background-color: #fff;
					padding: 16rpx;
					border-radius: 8rpx;
				}

				.class-item:last-child {
					min-height: 100vh;
				}

				.item-title {
					font-size: 26rpx;
					color: $u-main-color;
					font-weight: bold;
				}

				.item-menu-name {
					font-weight: normal;
					font-size: 24rpx;
					color: $u-main-color;
				}

				.item-container {
					display: flex;
					flex-wrap: wrap;
				}

				.thumb-box {
					width: 100%;
					display: flex;
					align-items: center;
					justify-content: center;
					// flex-direction: column;
					margin-top: 20rpx;
				}

				.item-menu-image {
					width: 120rpx;
					height: 120rpx;
				}

				.right {
					width: calc(100% - 120rpx);
					height: 100%;
					display: flex;
					flex-wrap: wrap;
					padding-left: 10rpx;
					position: relative;
					// background-color: pink;

					.name {
						width: 100%;
						display: flex;
						align-items: center;
						font-size: 28rpx;
						font-weight: bold;

						i {
							font-size: 28rpx;
							margin-right: 8rpx;
							color: #5098FF;
						}
					}

					.info {
						width: 100%;
						font-size: 24rpx;
						// background-color: yellow;
					}

					.desc {
						width: 100%;
						font-size: 24rpx;
						color: #c7c7c7;
					}

					.selectBtn {
						position: absolute;
						right: 0;
						top: 30rpx;
						padding: 10rpx;
						font-size: 26rpx;
						background-color: #5098FF;
						color: #fff;
						border-radius: 20rpx;
					}
				}
			}


		}
	}
</style>
